












import Vue from "vue";
import { Generator } from "@/generator";

export default Vue.extend({
  name: "Paragraph",

  data: function() {
    return {
      value: 35 as number,
      numOfItems: 3 as number
    };
  },

  methods: {
    lorem: function(num: number) {
      const minWords = 3;
      const maxWords = 10;

      return new Generator(minWords, maxWords).generateSentences(num);
    }
  },

  computed: {}
});
